body {
  margin: 0;
  font-family: 'Segoe UI', sans-serif;
  background-color: #0f0f0f;
  color: white;
}

input, button {
  font-size: 16px;
  border-radius: 4px;
  border: none;
  outline: none;
}

button {
  background-color: #ff0000;
  color: white;
  cursor: pointer;
}

button:hover {
  background-color: #cc0000;
}
